	module.exports = {"data":{"markdownRemark":{"html":"<p>当時 N88-日本語BASIC(86) を駆使して製作中だった RPG、 “<a href=\"/6338\">滅びた城の宝 Ⅰ</a>” のイメージイラスト。\nリアル系イラストの画力はこんなもん。</p>\n<p>本当はオープニングムービーなんかも妄想していて、その絵コンテのワンシーンだった記憶がある。\n確か三人のキャラデザを組んでいた。惜しくも残り二人は消失。</p>\n<p>なお実際のゲームではどう表現なされたかというと、なんと……  <strong>“●”</strong> だった。\n三つの ● ではなく、三人まとめて一つの ● 。\nパックマン以上(切れ込みないし)の手抜きっぷり。キャラデザなんて最初からなかったんや。</p>","frontmatter":{"cover":null,"date":"1995-11-01T10:00:43+09:00","strDate":"1995/11/1","title":"滅びた城の宝 Ⅰ イメージイラスト"}}},"pathContext":{"path":"/ja/blog/1995/11/01-treasure-in-grave/","langKey":"ja"}}

